* {
  margin: 0 auto;
  box-sizing: border-box;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: aliceblue;
}
div {
  height: 100%;
}
mesh {
  color: green;
}
h1 {
  position: absolute;
  left: 50%;
  top: 50%;
  font-size: 100px;
  transform: translate(-50%, -50%);
  background: peru;
  border: 4px solid black;
  z-index: 3;
}
h2 {
  position: absolute;
  right: 50%;
  bottom: 0%;
  font-size: 20px;
  transform: translate(50%, 0);
  background: peru;
  border: 4px solid black;
}
h3 {
  position: absolute;
  color: rgb(3, 3, 2);
  left: 50%;
  font-size: 30px;
  transform: translateX(-50%);
  z-index: 10;
  border: 3px solid black;
}
h4 {
  position: absolute;
  left: 50%;
  top: 50%;
  font-size: 40px;
  transform: translate(-50%, -50%);
  background: peru;
  border: 4px solid black;
  z-index: 3;
}
a {
  text-decoration: none;
  color: black;
}
